import React, { useState } from "react";
import "./styles.css";

var emojiDictionary = {
  "ü•ü": "Dumpling",
  "ü•†": "Fortune Cookie",
  "üçÆ": "Custard",
  "ü•ß": "Pie",
  "üç©": "Doughnut",
  "ü•ê": "Croissant",
  "ü•ñ": "Baguette Bread",
  "ü•®": "Pretzel",
  "ü•û": "Pancakes",
  "üßá": "Waffle",
  "üç¢": "Oden",
  "üç§": "Fried Shrimp",
  "üç•": "Naruto",
  "ü•Æ": "Moon Cake",
  "üç°": "Dango",
  "ü•ì": "Bacon",
  "üçî": "Hamburger",
  "üçü": "French Fries",
  "üçï": "Pizza",
  "üå≠": "Hot Dog",
  "ü•™": "Sandwich",
  "üåÆ": "Taco",
  "üåØ": "Burrito",
  "üßÜ": "Falafel",
  "ü•ó": "Green Salad",
  "üç±": "Bento Box",
  "üçò": "Rice Cracker",
  "üçô": "Rice Ball",
  "üçõ": "Curry Rice"
};
var emojiWeHave = Object.keys(emojiDictionary);

export default function App() {
  var [meaning, setMeaning] = useState("");

  function emojiInputHandler(event) {
    var userInput = event.target.value;
    var meaning = emojiDictionary[userInput];

    if (meaning === undefined) {
      meaning = "Sorry given emoji is not available";
    }
    setMeaning(meaning);
  }
  function emojiClickHandler(emoji) {
    var meaning = emojiDictionary[emoji];
    setMeaning(meaning);
  }

  return (
    <div className="App">
      <nav className="navigation">
        <h1>Know Your Food Emoji</h1>
      </nav>
      <br />
      <input
        className="input-emoji"
        onChange={emojiInputHandler}
        placeholder="Enter food emoji here"
        style={{
          display: "block",
          width: "70%",
          minHeight: "8vh",
          fontSize: "medium",
          paddingLeft: "1rem"
        }}
      ></input>
      <h3 className="meaning">
        <em>{meaning}</em>{" "}
      </h3>
      <div className="menu-card container-fluid">
        <h2 style={{ textAlign: "center", paddingBottom: "1rem" }}>
          Menu Card
        </h2>
        {emojiWeHave.map(function (emoji) {
          return (
            <span
              onClick={() => emojiClickHandler(emoji)}
              style={{
                fontSize: "1.5rem",
                padding: ".7rem",
                cursor: "pointer",
                textAlign: "centre"
              }}
              key={emoji}
            >
              {emoji}
            </span>
          );
        })}
      </div>
      <footer>
        <div id="footer-heading">About</div>
        <p>
          Often while chit-chatting with friends or family members on social
          media, we are unable recognize meaning of emoji sent. This app will
          help you to figure out meaning of food emoji that you don't know.{" "}
        </p>
      </footer>
    </div>
  );
}
